.btn-tr {
  cursor: pointer;
  color: #fff;
  background-color: #ff6b08;
  border: none;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 13px 46px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  display: flex;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  max-width: 450px;
  width: 100%;
  height: 609px;
  background-color: #fff;
  padding: 40px;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%)scale(1);
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(1.1);
}

.video-block {
  width: 100%;
  max-width: 500px;
  height: 100vh;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.video {
  width: 100%;
  aspect-ratio: 16 / 9;
  background-image: url("play-button.60e9dd92.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 20%;
  position: relative;
}

.video:hover {
  cursor: pointer;
  background-size: 22%;
}

.video iframe {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

